/**
 * Label的本体, 继承自L.layer
 */

import './label.scss';
import Layer from '../Layer';
import { Util, Point, DomUtil, CRS } from 'leaflet';

/**
 * Label, 一般性的文字标注
 * 
 * @export
 * @class Label
 * @extends {Layer}
 * @param {Array} latlng 和官方实现一样的接口
 * @param {String} label 要显示的文字
 * @param {Object} options 要覆盖的选项
 */
export default class Label extends Layer {
  constructor(pos, label, options = {}) {
    super(options);
    this._pos = pos;
    this._text = label;
    this._container = this._createContainer();
    // this._options = { pane: 'markerPane' };
  }
  /**
   * 这个是框架的钩子, 会在Layer.js里有调用
   *
   * @param {Map} map
   * @see leaflet/src/layer/Layer.js
   * 
   * @memberof Label#
   */
  onAdd(map) {
    this._container.innerHTML = this._text;
    this.getPane(map).appendChild(this._container);
    this._latlng = map.options.crs.projection.unproject(new Point(this._pos[0], this._pos[1]));
    this.updatePos();
  }
  onRemove(map) {
    return this.getPane(map).removeChild(this._container);
  }
  getEvents() {
		return {
			zoom: this.updatePos,
			viewreset: this.updatePos,
      zoomanim: this._animateZoom
		};
	}
  /**
   * 设置位置
   * 
   * @memberof Label#
   */
  updatePos() {
    var pos = this._map.latLngToLayerPoint(this._latlng).round();
		this._setPos(pos);
  }
  /**
   * 获得文字的大小
   *
   * @return {Object}
   * @memberof Label#
   */
  getSize() {
    if (!this._size) {
      this._size = {
        width: this._container.clientWidth,
        height: this._container.clientHeight
      };
    }
    return this._size;
   
  }
  /**
   * 动态设置内容
   * 
   * @param {String} text 
   * 
   * @memberOf Label#
   */
  setContent(text) {
    if (text) {
      this._container.innerHTML = this._text = text;
      this._size = null;
      this.updatePos()
    }
  }
  /**
   * 
   * @override 
   * @param {any} latlng 
   * 
   * @memberOf Label#
   */
  setLatLngs(latlng) {
    this._latlng = latlng;
    this.updatePos();
  }
  _setPos(pos) {
    DomUtil.setPosition(this._container, pos);
  }
  _createContainer() { 
    return DomUtil.create('div', this.getLabelClassName());
  }
  /**
   * @protected 
   * @param {Array.<String>} classList 
   * @return {String}
   * @memberof Label#
   */
  getLabelClassName(classList) {
    const defaultList = ['ffan-map-label'];
    return defaultList.concat(classList || []).join(' ');
  }
  _animateZoom(opt) {
		var pos = this._map._latLngToNewLayerPoint(this._latlng, opt.zoom, opt.center).round();
		this._setPos(pos);
	}
}


