/**
 * 在地图配置开启交互时, 激活交互服务.
 * 主要是在内部包装好针对Feature Click做封装, 提供相应的数据结构, 或许还有些方法
 */

import { EventTypes } from '../events';
import Store from '../ui/store/Store';


let map, featureGroup = null;

/**
 * 初始化, 也是激活
 * 
 * @param {LeafletMap} leafletMap 
 * @param {FeatureGroup} features 
 */
function initialize(leafletMap) {
  map = leafletMap;
}
/**
 * 每次切换加载新数据的时候都获取一次, 重新绑定事件
 * 
 */
function setFeatureGroup(features) {
  removeEvent();
  featureGroup = features;
  addEvent();
}
/**
 * 添加事件
 * 
 */
function addEvent() {
  if (featureGroup) {
    featureGroup.on('click', featureClickHandler);
  }
}
function removeEvent() {
  if (featureGroup) {
    featureGroup.off('click', featureClickHandler);
  }
}

function featureClickHandler(event) {
  // console.log(event);
  // console.log(event.target instanceof L.FeatureGroup);
  map.fire(EventTypes.FEATURE_CLICK, {
    containerPoint: event.containerPoint,
    latlng: event.latlng,
    layerPoint: event.layerPoint,
    type: EventTypes.FEATURE_CLICK,
    store: new Store(event.layer),
    originalEvent: event.originalEvent
  });
}


export default {
  initialize,
  setFeatureGroup
};


