const defaultConfig = {
  poiInteractive: false,
  poiInteractiveOptions: {
    mouseover: {
      fillColor: "#d5ebab",
      color: "#9bceb2",
      weight: 1,
      opacity: 1,
      fillOpacity: 1
    },
    click: {
      fillColor: "#d5ebab",
      color: "#9bceb2",
      weight: 1,
      opacity: 1,
      fillOpacity: 1
    },
    selected: {
      fillColor: "#f4ffa9",
      color: "#ff8b00"
    }
  }
};

export default defaultConfig;

export function merge(config) {
  if (config.hasOwnProperty('poiInteractiveOptions')) {
    const interactive = config.poiInteractiveOptions;
    if (interactive.hasOwnProperty('mouseover')) {
      Object.assign(defaultConfig.poiInteractiveOptions.mouseover, interactive.mouseover);
    }
    if (interactive.hasOwnProperty('click')) {
      Object.assign(defaultConfig.poiInteractiveOptions.click, interactive.click);
    }
    if (interactive.hasOwnProperty('selected')) {
      Object.assign(defaultConfig.poiInteractiveOptions.selected, interactive.selected);
    }
  }
  if (config.hasOwnProperty('poiInteractive')) {
    defaultConfig.poiInteractive = config.poiInteractive;
  }
}


