import 'leaflet/dist/leaflet.css';
import './index.scss';
import L, { CRS, LatLng, Point, FeatureGroup } from 'leaflet';
import FloorControl from './controls/floor'
import { Marker } from './ui/marker';
import { Label } from './ui/label';
import { Popup } from './ui/popup';
import { createRender, RenderTypes, Render } from './render';
import { EventDispatcher, EventTypes } from './events';
import GlobalConfig from './configs'
import Polyline from './ui/poly/Polyline';
import Polygon from './ui/poly/Polygon';
import drawControl from './controls/draw';
import { mapDataModel } from './models';
import { mapDataUtils, getStoreFeatures, toOriginalCoordinates, latLngToPoint, pointToLatLng } from './utils';
import { mapDataService, searchService, floorChangeService, drawService, interactiveService } from './services';
import loading from './ui/loading';

'use strict';

// ==== private member =====

let 
  /**
   * map instance
   * @type {leafletMap}
   * @private
   */
  map = null,
  /**
   * poi feature group
   * @type {GeoJson}
   * @private
   */
  featureGroup = null;


/**
 * 地图SDK入口, 用此类初始化, 渲染地图, 监听各种事件
 *
 * @export
 * @class FFanMap
 * @extends {EventDispatcher}
 *
 * @param {String} id     容器的id
 * @param {Object} option 地图相关配置
 * 
 * | Option | Type | Default | Description | 
 * | :------ | :------ | :------- | :------- |
 * | doubleClickZoom | Boolean | true | 是否开启双击放大
 * | dragging | Boolean | true | 是否开启拖拽
 * | zoomControl | Boolean | true | 是否开启zoom control
 * | editable | Boolean | false | 是否开启编辑
 * | drawControl | Boolean | false | 是否开启绘图, 开始绘图时, editable需要设置为 `true`
 * 
 */
export default class FFanMap extends EventDispatcher {
  constructor(id, option = {}) {
    super();
    const mapOption = Object.assign({}, GlobalConfig.map, option);
    mapOption.editOptions = GlobalConfig.draw;
    map = new L.Map(id, mapOption);
    map.getRenderer(map).options.padding = 100;
    mapDataModel.crs = map.options.crs;
    this._initFloorControl(map);
    drawService.initialize(map);
    if (option.regionInteractive) { interactiveService.initialize(map); }
    drawControl.initialize(map);
    loading.initialize(map);
    this.mapRender = createRender(RenderTypes.INDOOR, map);
    this.injectDispatcher(map);
    mapDataService.on(EventTypes.GET_MAP_DATA_SUCCESS, this._changeFloorDataSuccessHandler, this);
  }
  /**
   * 加载建筑数据
   * 
   * @param {String} id plazaID
   * 
   * @memberOf FFanMap#
   */
  loadBuilding(id) {
    // 这里重新加载广场了, 所以中心点要重新计算一次
    mapDataModel.mapCenterPoint = null;
    mapDataService.getData(id);
    // 这里是广场级别的响应, 实际上在每个广场内只会执行一次
    mapDataService.once(EventTypes.GET_MAP_DATA_SUCCESS, event => {
      const floorData = event.floorData, regionData = event.regionData;
      this._setFloorInfo(floorData.floors, floorData.currentFloor);
    });
  }
  /**
   * 绘制地图
   * 
   * @private
   * @param {Object} data 
   * 
   * @memberOf FFanMap#
   */
  render(data) {
    this.mapRender.render(data, { crs: map.options.crs })
      .then(poiFeatureGroup => {
        // map.setView(GlobalConfig.map.center || map.getCenter(), GlobalConfig.map.zoom || 20);
        featureGroup = new FeatureGroup(getStoreFeatures(poiFeatureGroup));
        if (map.options.regionInteractive) { interactiveService.setFeatureGroup(featureGroup); }
        this.fire(EventTypes.RENDERED);
      });
  }
  /**
   * 设置楼层信息, 如不调用此方法, 则不会启动楼层控件
   * 中途改私有了, 因为数据请求的内部化, 外部没有必要再关心楼层上的概念了
   *
   * @private
   * @param {Array.<Int>} floorList 类似这样 *[4, 3, 2, 1, -1, -2]* 这样的信息来描绘楼层
   * @param {Object} [options={}]
   *
   * @memberOf FFanMap#
   * 
   */
  _setFloorInfo(floorList, currentFloor) {
    this._floorcontrol.activate(floorList, currentFloor);
    if (!this._floorcontrol.getContainer()) {
      map.addControl(this._floorcontrol);
    } else {
      this._floorcontrol.updateUI();
    }
  }
  /**
   * 添加层, 这里可以添加所有自定义的层, 准确的说是继承自Layer的实例, 或是实现了ILayer接口的实例
   * 
   * @param {Layer} layer 继承自Layer的实例, 或是实现了ILayer接口的实例
   * 
   * @memberof FFanMap#
   */
  addOverlay(layer) {
    layer.addTo(map);
  }

  /**
   * 移除层
   * 
   * @param {Layer} layer 
   * 
   * @memberof FFanMap#
   */
  removeOverlay(layer) {
    map.removeLayer(layer);
  }
  /**
   * zoom in
   *
   * @param {Number} [zoom=null] zoom level
   * @param {Object} [options=null]
   * 会有*animate*选项
   *
   * @memberOf FFanMap#
   */
  zoomIn(zoom = null, options = null) {
    map.zoomIn(zoom = null, options = null);
  }
  /**
   * zoom out
   *
   * @param {Number} [zoom=null] zoom level
   * @param {Object} [options=null] 
   * 会有*animate*选项
   *
   * @memberOf FFanMap#
   */
  zoomOut(zoom = null, options = null) {
    map.zoomOut(zoom = null, options = null);
  }
  /**
   * 选择楼层, 会引起FloorControl的UI变化, 并触发楼层变更(*FLOOR_CHANGE*)事件
   *
   * @param {Int} floor
   *
   * @memberOf FFanMap#
   */
  chooseFloor(floor) {
    this._floorcontrol.choose(floor);
  }
  /**
   * 根据商店名称搜索
   * @param {Strng} name 要搜索的商店名称
   * 
   * @memberof FFanMap#
   */
  serachStoreByName(name) {
    return searchService.searchStoreByName(name, featureGroup);
  }
  /**
   * 根据门店ID来搜索门店
   * 
   * @param {String} id 
   * @return {Array}
   * 
   * @memberof FFanMap#
   */
  searchStoreByID(id) {
    return searchService.searchStoreByID(id, featureGroup);
  }
  /**
   * 设置地图的初始位置和层级
   * 
   * @param {Array} center 
   * @param {Number} zoom 
   * @return {FFanMap}
   * 
   * @memberof FFanMap#
   */
  setView(center, zoom) {
    const latlng = map.options.crs.projection.unproject(new Point(center[0], center[1]));
    map.setView(latlng, zoom);
    return this;
  }
  /**
   * 开始绘制多折线
   * 
   * @param {Object} [styles=null] 绘制时定制的样式
   * 
   * @memberOf FFanMap#
   */
  startPolyline(styles = null) {
    drawService.startPolyline(styles);
  }
  /**
   * 开始绘制多边形
   * 
   * @param {Object} [styles=null] 绘制时定制的样式
   * 
   * @memberOf FFanMap#
   */
  startPolygon(styles = null) {
    drawService.startPolygon(styles);
  }
  /**
   * 初始化楼层控制
   * 
   * @private
   * @param {LeafletMap} map 
   * 
   * @memberOf FFanMap#
   */
  _initFloorControl(map) {
    this._floorcontrol = new FloorControl();
    floorChangeService.initialize(this._floorcontrol, this);
  }
  _changeFloorDataSuccessHandler(event) {
    this.render({ floor: event.floorData, region: event.regionData });
  }
}

FFanMap.EventTypes = EventTypes;
FFanMap.Marker = Marker;
FFanMap.Label = Label;
FFanMap.Popup = Popup;
FFanMap.Polyline = Polyline;
FFanMap.Polygon = Polygon;
/**
 * @class Utils
 */
FFanMap.Utils = {
  toOriginalCoordinates,
  latLngToPoint,
  pointToLatLng
};

export { FFanMap }



